* {
  box-sizing: border-box;
}

body {
  margin: 0;
}

.search-form {
  width: 300px;
  margin: 0 auto;
  padding: 20px 0;
  position: relative;
}

.search-form input {
  width: 100%;
  height: 42px;
  color: #000;
  background-color: #fcf4de;
  border: rgba(0, 0, 0, 0);
  border-radius: 5px;
  outline: none;
  padding-left: 10px;
  box-shadow: 0 1px 3px rgba(0, 0, 0, .2), 0 1px 1px rgba(0, 0, 0, .14), 0 2px 1px -1px rgba(0, 0, 0, .12);
}

.search-form button {
  height: 42px;
  background-color: #fcf4de;
  border: rgba(0, 0, 0, 0);
  border-radius: 4px;
  padding: 6px 22px;
  font-size: 14px;
  transition: background-color .25s cubic-bezier(.4, 0, .2, 1), box-shadow .25s cubic-bezier(.4, 0, .2, 1);
  position: absolute;
  right: -100px;
  box-shadow: 0 1px 3px rgba(0, 0, 0, .2), 0 1px 1px rgba(0, 0, 0, .14), 0 2px 1px -1px rgba(0, 0, 0, .12);
}

.search-form button:hover, .search-form button:focus {
  cursor: pointer;
  background-color: #efe0b7;
  box-shadow: 0 3px 1px rgba(0, 0, 0, .1), 0 1px 2px rgba(0, 0, 0, .08), 0 2px 2px rgba(0, 0, 0, .12);
}

.search-form button:before {
  content: "";
  font-family: FontAwesome;
  font-size: 16px;
}

.load-more {
  height: 42px;
  background-color: #fcf4de;
  border: rgba(0, 0, 0, 0);
  border-radius: 4px;
  margin: 10px auto;
  padding: 6px 22px;
  font-size: 14px;
  transition: background-color .25s cubic-bezier(.4, 0, .2, 1), box-shadow .25s cubic-bezier(.4, 0, .2, 1);
  display: block;
  box-shadow: 0 1px 3px rgba(0, 0, 0, .2), 0 1px 1px rgba(0, 0, 0, .14), 0 2px 1px -1px rgba(0, 0, 0, .12);
}

.load-more:hover, .load-more:focus {
  cursor: pointer;
  background-color: #efe0b7;
  box-shadow: 0 3px 1px rgba(0, 0, 0, .1), 0 1px 2px rgba(0, 0, 0, .08), 0 2px 2px rgba(0, 0, 0, .12);
}

.gallery {
  flex-wrap: wrap;
  flex-basis: calc(25% - 20px);
  justify-content: center;
  gap: 20px;
  margin-left: auto;
  margin-right: auto;
  padding: 0;
  list-style: none;
  display: flex;
}

.photo-card {
  position: relative;
  box-shadow: 0 1px 3px rgba(0, 0, 0, .2), 0 1px 1px rgba(0, 0, 0, .14), 0 2px 1px -1px rgba(0, 0, 0, .12);
}

.gallery-image {
  height: auto;
  object-fit: cover;
  display: block;
}

.photo-link {
  color: var(--main-text-color);
  height: 100%;
  background-color: #fffcf4;
  text-decoration: none;
  transition: box-shadow .25s cubic-bezier(.4, 0, .2, 1);
  display: block;
}

.photo-link:hover {
  cursor: zoom-in;
  box-shadow: 0 1px 1px rgba(0, 0, 0, .12), 0 4px 4px rgba(0, 0, 0, .06), 1px 4px 6px rgba(0, 0, 0, .16);
}

.info {
  color: #000;
  padding-left: 20px;
  font-size: 14px;
}

.info-item {
  font-size: 20px;
}

